te sobre todo cuando se vea afectada su integridad personal y la de su familiar.")])])]),_c('div',{staticClass:"col-lg-4 col-md-9 mb-4",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t5/img37.png"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-4"},[_c('div',{staticClass:"col-lg-5 mb-4",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"bg-1 h-100"},[_c('div',{staticClass:"bg-acento-contenido p-3"},[_c('h5',{staticClass:"t-yellow text-center mb-0"},[_vm._v("CÓDIGO LEY 600 DE 2000")])]),_c('ul',{staticClass:"lista-ul--color fa-ul circulo mb-0 p-4"},[_c('li',{staticClass:"mb-1"},[_c('i',{staticClass:"fas fa-circle"}),_vm._v("Poca participación de las víctimas en los procesos.")]),_c('li',{staticClass:"mb-1"},[_c('i',{staticClass:"fas fa-circle"}),_vm._v("Poca reparación.")]),_c('li',{staticClass:"mb-1"},[_c('i',{staticClass:"fas fa-circle"}),_vm._v("Existía mucha reglamentación.")])])])]),_c('div',{staticClass:"col-lg-5 mb-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"bg-1 h-100"},[_c('div',{staticClass:"bg-acento-contenido p-3"},[_c('h5',{staticClass:"t-yellow text-center mb-0"},[_vm._v("CÓDIGO LEY 906 DE 2004")])]),_c('ul',{staticClass:"lista-ul--color fa-ul circulo mb-0 p-4"},[_c('li',{staticClass:"mb-1"},[_c('i',{staticClass:"fas fa-circle"}),_vm._v("Las víctimas interactúan en los procesos.")]),_c('li',{staticClass:"mb-1"},[_c('i',{staticClass:"fas fa-circle"}),_vm._v("Busca respetar los tres derechos: verdad, justicia y reparación.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-start mb-4"},[_c('div',{staticClass:"col-auto",attrs:{"data-aos":"fade"}},[_c('div',{staticClass:"list"},[_c('div',{staticClass:"list__item"},[_c('div',{staticClass:"list__item__icon"},[_c('img',{attrs:{"src":require("@/assets/curso/tema